import React from 'react';
import { SingleInputModal } from '../common/Modal';

/**
 *   
 */

const InputBarcodeModal = ({
  className,
  visible,
  onSubmit,
  onCancel,
  onChange,
  value,
  error,
  resultMessage,
}) => {
  return (
    <SingleInputModal
      className={className}
      visible={visible}
      title=" "
      description="    ."
      inputLabel="BARCODE"
      inputName="barcode"
      placeholder="13  "
      submitText=" "
      onSubmit={onSubmit}
      onCancel={onCancel}
      onChange={onChange}
      value={value}
      error={error}
      resultMessage={resultMessage}
    />
  );
};

export default InputBarcodeModal;
